import React, {Fragment, useState, useEffect} from 'react';
import { Container, Row, Alert, Col, Input, Button, Form, FormGroup, Label } from 'reactstrap';
import { useSelector, useDispatch } from "react-redux";
import { useHistory } from "react-router-dom";
import axios from "axios";
import { USER_UPDATED } from "../../actions/types"

const personalities = [

    {type: "ISTJ - The Logistician",value:"ISTJ", motivated: "Analyzing their surroundings", stuggle: "Indecisiveness"},
    {type: "ISTP - The Virtuoso",value:"ISTP", motivated: "Making things better", stuggle: "Being interfered with"},
    {type: "ISFJ - The Defender",value:"ISTJ", motivated: "Analyzing their surroundings", stuggle: "Indecisiveness"},
    {type: "ISFP - The Adventurer",value:"ISFP", motivated: "Analyzing their surroundings", stuggle: "Indecisiveness"},
    {type: "INFJ - The Advocate",value:"INFJ", motivated: "Analyzing their surroundings", stuggle: "Indecisiveness"},
    {type: "INFP - The Mediator",value:"INFP", motivated: "Analyzing their surroundings", stuggle: "Indecisiveness"},
    {type: "INTJ - The Architect",value:"INTJ", motivated: "Analyzing their surroundings", stuggle: "Indecisiveness"},
    {type: "INTP - The Logician",value:"INTP", motivated: "Analyzing their surroundings", stuggle: "Indecisiveness"},
    {type: "ESTP - The Entrepreneur",value:"ESTP", motivated: "Analyzing their surroundings", stuggle: "Indecisiveness"},
    {type: "ESTJ - The Executive",value:"ESTJ", motivated: "Analyzing their surroundings", stuggle: "Indecisiveness"},
    {type: "ESFP - The Entertainer",value:"ESFP", motivated: "Analyzing their surroundings", stuggle: "Indecisiveness"},
    {type: "ESFJ - The Consul",value:"ESFJ", motivated: "Analyzing their surroundings", stuggle: "Indecisiveness"},
    {type: "ENFP - The Campaigner",value:"ENFP", motivated: "Analyzing their surroundings", stuggle: "Indecisiveness"},
    {type: "ENFJ - The Protagonistn",value:"ENFJ", motivated: "Analyzing their surroundings", stuggle: "Indecisiveness"},
    {type: "ENTP - The Debater",value:"ENTP", motivated: "Analyzing their surroundings", stuggle: "Indecisiveness"},
    {type: "ENTJ - The Commandern",value:"ENTJ", motivated: "Analyzing their surroundings", stuggle: "Indecisiveness"},
    
    
];

const Profile = () => {
    const isAuthenticated = useSelector((state) => state.auth.isAuthenticated);
    const profile = useSelector((state) => state.auth.profile);
    let history = useHistory();
    const dispatch = useDispatch();
    const [name, setName] = useState('')
    const [mbti, setMbti] = useState('')
    const [error, setError] = useState(null)

    useEffect(() => {

        if(!isAuthenticated){
            history.push("login") 
        }

    }, [isAuthenticated])

    useEffect(() => {
        

        if(profile){
            axios.post("/api/update", {
                email: profile.email,
                mbti: mbti
            }).then((res) => {
                dispatch({
                    type:USER_UPDATED,
                    payload:{
                     profile: res.data
                    }});              
                
            }
            ).catch((err) => {
                console.log(err)
                
            });
        }
        
        

    }, [mbti])

    

	return (
        <Container style={{background: "#FFFCF4"}}>
          
            <Row className="d-flex justify-content-center pt-5 pb-5">
                <img widht="40" src="https://res.cloudinary.com/worthmore/image/upload/v1614288828/TechChallenge/logo-text.svg" />
            </Row>
            <Row>
                <Col>
                    <h2 className="text-center">Hello {profile ? profile.name : null} </h2>
                </Col>
            </Row>
            {
            error ?  <Alert block color="danger">
            {error}
          </Alert> : null
        }
        
        <Form>
      
      <FormGroup tag="fieldset">
        <legend className="text-center">Choose your MBTI type:</legend>
        
        {personalities.map(type => 
            <FormGroup check>
            <Label check>
                <Input onChange={event => setMbti(event.target.value)} type="radio" name="mbti" value={type.value} />{' '}
                {type.type}
            </Label>
            </FormGroup>

        )}
        
      </FormGroup>
      
      
     <Button className="p-3" style={{background: "#FFDC7D", borderRadius: "40px", color:"#898989", fontWeight: "600" }} onClick={()=>history.push("/play")} color="primary" block>Start game</Button>
     
    </Form>
        </Container>
	);
};

export default Profile;
